body{
  margin: 0;
}

*{
  box-sizing: border-box;
}

.engine-slider {

  // .engine-slider__container

  &__container {
    max-width: 1200px;
    overflow: visible;
  }

  // .engine-slider__wrapper

  &__wrapper {
  }

  // .engine-slider__slede

  &__slede {
    width: 500px;
    margin-right: 15px;
    @media (max-width:771px){
      margin-right: 0;
    }
    @media (max-width:610px){
      margin-right: 15px;
    }
    @media (max-width:450px){
      margin-right: 9px;
    }
  }

  // .engine-slider__contant

  &__contant {
    
  }

  // .engine-slider__link

  &__link {
    
  }

  // .engine-slider__back

  &__back {
    background: url('../img/back-slider1.jpg');
    background-repeat: no-repeat;
    background-position: center center;
    background-size:cover;
    border-radius: 8px;
    width: 500px;
    height: 200px;
    padding: 24px 32px;
    display: flex;
    flex-direction: column;
    @media (max-width:991px) {
      width: 400px;
      height: 150px;
    }
    @media (max-width:771px){
      width: 300px;
      height: 100px;
    }
  }

  // .engine-slider__title

  &__title {
    font-weight: 500;
    font-size: 20px;
    color: rgba(51, 51, 51, 0.8);
    margin-top: auto;
    margin-bottom: 5px;
    @media (max-width:771px){
      font-size: 18px;
    }
  }

  // .engine-slider__sub-title

  &__sub-title {
    font-weight: 400;
    font-size: 16px;
    color: rgba(51, 51, 51, 0.64);
    @media (max-width:771px){
      font-size: 14px;
    }
  }

  &__title, &__sub-title {
    text-decoration: none;
    font-family: 'Arial';
    display: block;
  }

}
.swiper-container {
}
.swiper-wrapper {
}
.swiper-slide {
}


.engine-slider {

  // .engine-slider__arrow

  &__arrow {
    position: absolute;
  }

  // .engine-slider__arrow-prev

  &__arrow-prev {
    transform: rotate(180deg);
    left: 250px;
    margin-top: 89px;
    @media (max-width:1300px){
      left: 10px;
    }
    @media (max-width:991px) {
      margin-top: 60px;
    }
    @media (max-width:771px) {
      margin-top:  40px;
    }
  }

  // .engine-slider__arrow-prev

  &__arrow-next {
    right:  250px;
    margin-top: 89px;
    @media (max-width:1300px) {
      right: 10px;
    }
    @media (max-width:991px) {
      margin-top: 60px;
    }
    @media (max-width:771px) {
      margin-top:  40px;
    }
  }

}
.engine-slider__arrow-prev, .engine-slider__arrow-next{
    position: absolute;
    background-color: #fff;
    width: 30px;
    height: 30px;
    top: 0;
    z-index: 2;
    border-radius: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}
.engine-swiper__inner{
  position: relative;
  overflow: hidden;
}